/* Reset styles */

* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a {
  color: inherit;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

nav ul {
  list-style: none;
}

img {
  vertical-align: top;
}

nav,
footer,
header,
aside,
label {
  display: block;
}

input,
button,
textarea {
  font-family: inherit;
  color: inherit;
}

::placeholder {
  color: inherit;
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

.hide {
  opacity: 0;
}

.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #383B45;
  z-index: 100;
  display: grid;
  place-content: center;
}

.loader {
  display: grid;
  place-content: center;
  width: 125px;
  height: 125px;
  border: solid 10px rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  border-left-color: rgba(255, 255, 255, 0.7);
  animation: load 1.3s linear infinite;
}

@keyframes load {
  100% {
    transform: rotate(360deg);
  }
}

/* swiper-slider */

.swiper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
}

.swiper-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-pointer-events {
  touch-action: pan-y;
}

.swiper-pointer-events.swiper-vertical {
  touch-action: pan-x;
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */

.swiper-autoheight,
.swiper-autoheight .swiper-slide {
  height: auto;
}

.swiper-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

.swiper-backface-hidden .swiper-slide {
  transform: translateZ(0);
  backface-visibility: hidden;
}

/* 3D Effects */

.swiper-3d,
.swiper-3d.swiper-css-mode .swiper-wrapper {
  perspective: 1200px;
}

.swiper-3d .swiper-wrapper,
.swiper-3d .swiper-slide,
.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom,
.swiper-3d .swiper-cube-shadow {
  transform-style: preserve-3d;
}

.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}

.swiper-3d .swiper-slide-shadow {
  background: rgba(0, 0, 0, 0.15);
}

.swiper-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

/* CSS Mode */

.swiper-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none;
  /* For Firefox */
  -ms-overflow-style: none;
  /* For Internet Explorer and Edge */
}

.swiper-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}

.swiper-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-horizontal.swiper-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-vertical.swiper-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

.swiper-centered > .swiper-wrapper::before {
  content: "";
  flex-shrink: 0;
  order: 9999;
}

.swiper-centered.swiper-horizontal > .swiper-wrapper > .swiper-slide:first-child {
  margin-inline-start: var(--swiper-centered-offset-before);
}

.swiper-centered.swiper-horizontal > .swiper-wrapper::before {
  height: 100%;
  width: var(--swiper-centered-offset-after);
}

.swiper-centered.swiper-vertical > .swiper-wrapper > .swiper-slide:first-child {
  margin-block-start: var(--swiper-centered-offset-before);
}

.swiper-centered.swiper-vertical > .swiper-wrapper::before {
  width: 100%;
  height: var(--swiper-centered-offset-after);
}

.swiper-centered > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: center center;
}

/* global styles*/

body {
  font-family: "Noto-Sans", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  letter-spacing: 0.5px;
  color: #fff;
  background-color: #070707;
}

.container {
  margin: 0 auto;
  max-width: 1270px;
  padding: 0 10px;
}

.big-container {
  width: 100vw;
}

.logo__svg {
  width: clamp(111px, 6.1125rem + 3.52vw, 133px);
  aspect-ratio: 2.1/1;
  fill: #fff;
  transition: all 0.3s;
}

.logo:hover svg {
  fill: #b82121;
}

.button {
  position: relative;
  padding: 22px 53px;
  border-radius: 5px;
}

.button::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  border-radius: 5px;
  z-index: 1;
  background: inherit;
  filter: brightness(130%);
  transition: all 0.3s;
}

.button:hover::after {
  width: 100%;
}

.button span {
  position: relative;
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  z-index: 2;
}

/* Header styles */

.header {
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  padding-top: 10px;
}

.header__inner {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  gap: 20px;
}

.nav {
  width: 100%;
  max-width: 409px;
}

.nav__list {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.nav__link {
  display: block;
  position: relative;
  font-size: clamp(14px, 0.4612rem + 0.8621vw, 16px);
  font-weight: 500;
  transition: all 0.3s;
}

.nav__link::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  color: #b82121;
  overflow: hidden;
  transition: all 0.3s;
}

.nav__link:hover {
  color: transparent;
}

.nav__link:hover::after {
  width: 100%;
}

.header__contacts {
  width: 100%;
  max-width: 363px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.header__phone {
  max-width: 245px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 10px;
}

.header__phone-svg {
  width: 20px;
  height: 20px;
  fill: #fff;
  transition: all 0.3s;
}

.header__phone:hover .header__phone-svg {
  fill: #b82121;
  animation: dialing 1s linear 0.3s infinite;
}

.header__phone-number {
  position: relative;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: clamp(16px, 0.5219rem + 0.996vw, 21px);
  transition: all 0.3s;
}

.header__phone-number::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  color: #b82121;
  overflow: hidden;
  transition: all 0.3s;
}

.header__phone:hover .header__phone-number {
  color: transparent;
}

.header__phone:hover .header__phone-number::after {
  width: 100%;
}

.header__socials {
  width: 100%;
  max-width: 90px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.header__social-link {
  width: 20px;
  height: 20px;
}

.header__social-svg {
  width: 20px;
  height: 20px;
  fill: #fff;
  transition: all 0.3s;
}

.header__social-link:hover .header__social-svg {
  fill: #b82121;
  animation: dialing 1s linear 0.3s infinite;
}

@keyframes dialing {
  0% {
    transform: scale(1.2);
  }

  25% {
    transform: scale(1.3);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.burger {
  display: none;
  width: 30px;
  height: 22px;
  position: relative;
  transition: all 0.3s;
  z-index: 10;
}

.burger:hover .burger__span {
  background-color: #b82121;
}

.burger__span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2.2px;
  border-radius: 1px;
  background-color: #fff;
  transition: all 0.3s;
}

.burger__span-one {
  top: 0;
}

.burger__span-two {
  top: 50%;
}

.burger__span-three {
  top: 100%;
}

.hero {
  width: 100%;
  padding-top: clamp(200px, 12.275rem + 0.96vw, 206px);
  padding-bottom: clamp(150px, 8.4rem + 4.16vw, 176px);
  background-image: url("../img/hero-bg.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.hero__inner {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}

.hero__title {
  width: 100%;
  max-width: 608px;
  margin-bottom: clamp(60px, 2.4375rem + 5.6vw, 95px);
  font-weight: 800;
  font-size: clamp(32px, 1.1375rem + 3.68vw, 55px);
  line-height: 1.27;
  letter-spacing: -0.5px;
}

.hero__title span {
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  color: #b82121;
  letter-spacing: -1.2px;
}

.hero__descr {
  width: 100%;
  max-width: 501px;
  margin-bottom: 35px;
}

.hero__button {
  background-color: #b82121;
}

.menu {
  width: 100%;
  padding-top: 50px;
}

.title {
  font-weight: 900;
  font-size: 48px;
  font-size: clamp(28px, 1.1034rem + 2.7586vw, 48px);
  line-height: 1.45833;
  text-align: center;
}

.menu__title {
  margin-bottom: 25px;
}

.menu__descr {
  text-align: center;
  margin: 0 auto 63px;
  max-width: 632px;
}

.menu__slider {
  width: 100%;
}

.menu__slider-track .swiper-slide {
  flex-shrink: 1;
}

.menu__slider-track .swiper-slide-active .menu__button {
  translate: 0 0 0;
  opacity: 1;
  z-index: 10;
}

.menu__slider-track .swiper-slide-active .menu__item-overlay {
  translate: 0 -100% 0;
}

.menu__item {
  position: relative;
  width: 288px;
  aspect-ratio: 1/1.76;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.menu__item picture {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.menu__img {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  object-fit: cover;
}

.menu__button {
  position: relative;
  padding: 20px 55px;
  border: 2px solid rgba(255, 255, 255, 0.6);
  border-radius: 5px;
  background: rgba(7, 7, 7, 0.65);
  font-weight: 900;
  font-size: 14px;
  text-transform: uppercase;
  z-index: 10;
  translate: 0 -250px 0;
  opacity: 0;
  transition: translate 0.5s;
  cursor: pointer;
}

.menu__item-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgba(7, 7, 7, 0.7);
  transition: translate 0.5s;
}

.menu__item-title {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 30px;
  z-index: 2;
  font-weight: 800;
  font-size: 24px;
  text-align: center;
}

.swiper-button {
  position: absolute;
  top: 50%;
  translate: 0 -50% 0;
  z-index: 10;
}

.swiper-button-next {
  left: 0;
}

.swiper-button-prev {
  right: 0;
}

.arrow-svg {
  width: 40px;
  height: 40px;
  fill: #fff;
}

.galary {
  width: 100%;
  padding: 143px 0 0 0;
  background-image: url("../img/galary/galary-bg.png");
  background-size: 258px 223px;
  background-position: 10px 20px;
  background-repeat: no-repeat;
}

.galary__title {
  margin-bottom: 25px;
}

.galary__descr {
  text-align: center;
  margin: 0 auto 63px;
  max-width: 632px;
}

.galary__images {
  width: 100%;
}

.galary__images-track {
  display: grid;
  grid-template-columns: repeat(4, minmax(180px, 358px));
  place-content: center;
}

.galary__img-card {
  width: 100%;
  max-width: 358px;
  aspect-ratio: 1.33/1;
  overflow: hidden;
}

.galary__img-link {
  display: block;
  width: 100%;
  height: 100%;
}

.galary__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.3s;
}

.galary__img-card:hover .galary__img {
  scale: 1.05;
}

/* modals styles*/

.modal-window {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  padding: 15px;
  z-index: -50;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s;
  background: rgba(7, 7, 7, 0.3);
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-window--visible {
  z-index: 50;
  opacity: 1;
  pointer-events: all;
}

@media (min-width: 1440px) {
  .menu__item {
    width: 19.4vw;
    aspect-ratio: 1/1.76;
  }

  .menu__button {
    width: 85%;
    padding: 0;
    aspect-ratio: 3.81/1;
  }

  .galary__images-track {
    grid-template-columns: repeat(4, minmax(358px, 1fr));
  }

  .galary__img-card {
    max-width: 100%;
  }
}

@media (max-width: 1350px) {
  .hero__inner {
    padding-left: 100px;
  }

  .swiper-button-next {
    left: 80px;
  }

  .swiper-button-prev {
    right: 80px;
  }
}

@media (max-width: 1200px) {
  .header__contacts {
    max-width: 342px;
  }
}

@media (max-width: 1000px) {
  .nav {
    max-width: 350px;
  }

  .header__contacts {
    max-width: 320px;
  }
}

@media (max-width: 768px) {
  .logo {
    position: relative;
    z-index: 10;
  }

  .nav__link {
    font-size: 16px;
  }

  .header__contacts {
    max-width: 125px;
    z-index: 10;
  }

  .header__phone-number {
    display: none;
  }

  .burger {
    display: block;
  }

  .untranslate {
    transform: translateX(0);
  }

  .span-one-transform {
    top: 50%;
    transform: rotate(45deg);
    transition: all 0.3s;
  }

  .span-two-transform {
    transform: rotate(-45deg);
    transition: all 0.3s;
  }

  .span-three-transform {
    display: none;
  }

  .nav {
    position: fixed;
    top: 0;
    right: 0;
    width: 100vw;
    max-width: 100vw;
    height: 100dvh;
    flex-direction: column;
    justify-content: flex-start;
    translate: 100% 0 0;
    padding: 100px 0 0 0;
    background-color: #070707;
    z-index: 5;
    overflow: hidden;
  }

  .nav__list {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    gap: 20px;
  }

  .untranslate {
    translate: 0 0 0;
  }

  .nav__item {
    opacity: 0;
    transition: all 0.3s;
    translate: 0 200px 0;
  }

  .nav__item:nth-child(1) {
    transition-delay: 0.7s;
  }

  .nav__item:nth-child(2) {
    transition-delay: 0.8s;
  }

  .nav__item:nth-child(3) {
    transition-delay: 0.9s;
  }

  .nav__item:nth-child(4) {
    transition-delay: 1s;
  }

  .nav__item--animate {
    translate: 0 0 0;
    opacity: 1;
  }

  .hero__inner {
    padding-left: 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
  }

  .hero__title {
    text-align: center;
  }

  .menu__item {
    width: 238px;
  }

  .menu__button {
    padding: 15px 35px;
  }

  .menu__item-title {
    font-size: 18px;
  }

  .swiper-button-next {
    left: 30px;
  }

  .swiper-button-prev {
    right: 30px;
  }

  .galary__images-track {
    grid-template-columns: repeat(2, minmax(180px, 1fr));
  }

  .galary__img-card {
    max-width: 100%;
  }
}

@media (max-width: 600px) {
  .swiper-button-next {
    left: 20px;
  }

  .swiper-button-prev {
    right: 20px;
  }
}

@media (max-width: 376px) {
  .container {
    padding: 0 8px;
  }

  .big-container {
    width: 100%;
    margin: 0 auto;
    padding: 0 8px;
  }

  .menu__descr {
    margin: 0 auto 30px;
  }

  .menu__slider-track {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 10px;
  }

  .menu__item {
    width: 100%;
    max-width: 355px;
    aspect-ratio: 1.18/1;
    gap: 20px;
  }

  .menu__button {
    translate: 0 0 0;
    opacity: 1;
    order: 2;
  }

  .menu__item-overlay {
    background-color: rgba(7, 7, 7, 0.4);
  }

  .menu__item-title {
    position: static;
    order: 1;
  }

  .swiper-button {
    display: none;
  }

  .galary {
    padding: 50px 0 0 0;
    background-image: none;
  }

  .galary__images .swiper-pagination {
    width: 100%;
    height: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
  }

  .galary__images .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin: 0;
    margin: 0 5px;
    background-color: #c4c4c4;
    transition: all 0.3s;
  }

  .galary__images .swiper-pagination-bullet-active {
    background-color: #b82121;
  }

  .galary__images-track {
    min-width: 100%;
    display: flex;
    justify-content: start;
    margin-bottom: 30px;
  }

  .galary__images-track .swiper-wrapper {
    display: flex;
  }

  .galary__img-card {
    min-width: 100%;
  }
}

@media (max-width: 375px) {
  .logo__svg {
    width: clamp(100px, 2.25rem + 20vw, 111px);
  }

  .hero__title {
    text-align: center;
    font-size: clamp(26px, -0.5972rem + 11.1111vw, 32px);
  }

  .hero__descr {
    margin-bottom: 30px;
    text-align: center;
  }

  .hero__button {
    margin: 0 auto;
  }
}

@media (max-width: 360px) {
  .button span {
    font-size: 12px;
  }

  .hero__descr {
    font-size: 14px;
  }

  .menu {
    padding-top: 20px;
  }
}